@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;1,200;0,300;0,400,0,600;0,900;1,900&display=swap);
body{margin:0;padding:0;box-sizing:border-box;font-family:"Source Sans Pro", sans-serif;background-color:#f2eeeb;color:#1d2a40}*{box-sizing:border-box}

.intro-section{display:block;height:auto;max-width:100vw;padding:5% 10% 20% 10%}@media (max-width: 767px){.intro-section{height:auto;width:100vw}}.intro-section__name{display:block;font-size:8rem;font-weight:900;padding-top:5%;line-height:1.1em;font-family:Source Sans Pro, sans-serif}@media (max-width: 767px){.intro-section__name{font-size:4rem}}.intro-section__name--left{text-align:right;padding-right:50%}.intro-section__name--right{padding-left:50%}.intro-section__bio{font-size:2rem;font-weight:500;margin-top:10%;line-height:1.3em;font-family:Source Sans Pro, sans-serif;padding:0 20%;text-align:center}@media (max-width: 767px){.intro-section__bio{padding:25% 0 0 0;font-size:1rem;font-weight:400;margin-top:5%;padding:0 15%}}

.exp-section{width:100vw;justify-content:center;margin-bottom:10%;padding:0 15%}@media (max-width: 767px){.exp-section{height:auto;width:100vw}}.exp-tile{display:block;position:relative;width:100%;border-radius:20px;transition:all 0.3s ease-in-out;padding:3em 3em 4em;overflow-x:hidden;overflow-y:hidden}@media (max-width: 767px){.exp-tile{padding:10% 10% 10%}}.exp-tile:hover{box-shadow:1px 1px 1em #b3b8ba;cursor:pointer}.exp-tile:hover img{transform:translateY(-10px)}.exp-tile--1{background-image:linear-gradient(#dbcdff, #a2c7dc)}.exp-tile--2{background-image:linear-gradient(to top left, #fbd5e8, #ffeec4)}.exp-tile__category{text-align:right;display:block;font-size:14px;opacity:0.6;text-transform:uppercase;letter-spacing:1.5px;font-weight:700}@media (max-width: 767px){.exp-tile__category{font-size:7px;padding-left:50%}}.exp-tile__title{font-size:45px;text-align:right;display:block;margin-bottom:1em;letter-spacing:-1px}@media (max-width: 767px){.exp-tile__title{font-size:25px}}.exp-tile__holder{position:relative;max-width:100%;max-height:500px;margin:0 auto;text-align:center}.exp-tile__image{transition:all 0.3s ease-in-out;max-width:100%;max-height:500px;position:relative;border-radius:5px;display:inline-block;vertical-align:middle}

.contact-section{display:block;max-width:100vw;margin-bottom:10%;padding:0% 10% 0% 10%}@media (max-width: 767px){.contact-section{height:auto;width:100vw;margin-bottom:10%}}.contact-section__contact{font-size:2rem;font-weight:700;line-height:1.5em;padding:0 20%}@media (max-width: 767px){.contact-section__contact{font-size:1rem;font-weight:400;margin-top:5%;padding:0 20%}}.contact-section__links{margin-top:5%;padding:0 20%}.contact-section__link{cursor:pointer}.contact-section__icons{width:30px;height:30px;margin-right:30px;display:inline-block}.contact-section__icon{max-width:100%;max-height:100%}.contact-section__email{font-size:1.5rem;margin-top:10%;color:#f22283;font-family:inherit}@media (max-width: 767px){.contact-section__email{font-size:1rem}}.red-highlight{color:#f22283}

